<template>
    <div>
        <section v-for="item in display">
            <header>
                <span v-text="item.title"></span>
            </header>
            <article v-for="obj in item.fields">
                <el-row :gutter="gutter">
                    <el-col :span="6" class="text-right">
                        <span v-text="obj.showName"></span>
                    </el-col>
                    <el-col :span="18">
                        <span>{{obj.format?obj.format(data):data[obj.name]}}</span>
                    </el-col>
                </el-row>
            </article>
        </section>
    </div>
</template>
<script type="text/javascript">
    // 传入一个显示的数组
    export default {
        props: {
            data: Object,
            display: Array,
            gutter: Number
        }
    }
</script>
<style scoped>
header {
    font-family: MicrosoftYaHei;
    font-size: 18px;
    color: #696e79;
    margin-bottom: 15px;
    padding-top: 30px;
}
article {
    font-family: MicrosoftYaHei;
    font-size: 14px;
    color: #878787;
}
article span {
    line-height: 36px;
}
section {
    width: 490px;
    margin-bottom: 30px;
}
section:not(:first-child) {
    border-top: 1px #cad8df solid;
}
</style>



