<template>
    <div class="h-full">
        <div class="clearfix h-full" v-if="!isDetail">
            <div class="panel-nav">
                <el-breadcrumb separator="/" class="m-l-lg">
                    <el-breadcrumb-item>
                        {{$t('lessonGroup.name')}}
                    </el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <div class="panel-body-list clearfix">
                <list-table
                :data="srcData"
                :fields="fields"
                :operators="operators"
                class="wrapper-md"
                :operatorsTitle="$t('operate')"
                :isSearchLeft="false">
                    <div slot="btns-left">
                        <button class="btn btn-transform padder" @click="joinLessonGroup">{{$t('join')}}</button>
                    </div>
                    <div slot="btns">
                        <button class="fa fa-refresh btn-refresh" @click="refresh"></button>
                    </div>
                </list-table>
            </div>
        </div>
        <div class="clearfix h-full bg-white" v-else>
            <div class="container-header clearfix">
                <el-button @click="canel" class="btn btn-canel text-muted pull-left" icon="el-icon-arrow-left">
                    {{$t('canel')}}
                </el-button>
                <div class="pull-left m-l-md m-t-sm">
                    <span class="text-gray text-md">{{detailData.infoList.name}}</span><br>
                    <!-- <span class="text-muted text-xs l-h">{{$t('outTime')}}{{detailIndex}}</span>-->
                </div>
                <div class="pull-right m-t-xs">
                    <el-button class="btn btn-transparent">{{$t('compile')}}</el-button>
                    <el-button class="btn btn-transparent">{{$t('remove')}}</el-button>
                </div>
            </div>
            <div class="container-aside">
                <el-menu text-color="#696e79" active-text-color="#0c92fe" class="el-menu-vertical-demo"
                    default-active="baseInfo">
                    <el-menu-item index="baseInfo" @click="changeIndex('baseInfo')">
                        <i class="el-icon-menu"></i>
                        <span slot="title" v-text="$t('baseInfo')"></span>
                    </el-menu-item>
                    <el-menu-item index="comment" @click="changeIndex('comment')">
                        <i class="el-icon-menu"></i>
                        <span slot="title" v-text="$t('comment')"></span>
                    </el-menu-item>
                </el-menu>
            </div>
            <div class="container-main">
                <div v-if="detailIndex === 'baseInfo'">
                    <info-panel
                    :data="detailData.infoList"
                    :display="detailData.infoHandler"
                    class="m-l-xl"
                    :gutter="20"></info-panel>
                    <div class="m-l-xl">
                        <header class="m-b-md">{{$t('studentSelected')}}</header>
                        <list-table
                            key="studentSelected"
                            :data="detailData.studentSelected.list"
                            class="no-padder"
                            :fields="detailData.studentSelected.fields"
                            ></list-table>
                    </div>
                </div>
                <div v-if="detailIndex === 'comment'" class="container-main-detail clearfix">
                    <list-table
                    key="comment"
                    :data="commentData"
                    :fields="detailData.commentFields"
                    :operators-title="$t('operate')"
                    :cell-style="{borderBottomWidth: 0}"
                    :operators="detailData.commentOperators"
                    :operatorWidth="130">
                        <div slot="btns-bottom">
                            <el-button class="btn btn-manage">{{$t('canelComment')}}</el-button>
                        </div>
                    </list-table>
                </div>
            </div>
        </div>
        <tip-dialog
            key="join"
            useTemplate="true"
            :visible="joinData.visible"
            :title="joinData.title"
            @close="joinData.cancel"
            dialogClass="join-dialog"
            @confirm="joinData.confirm">
            <template>
                <div slot="body">
                    <div class="join-panel">
                        <div class="join-tip">
                            <div class="pull-left"><i class="fa fa-info"></i></div>
                            <span>{{$t('joinTip')}}</span>
                        </div>
                        <div class="join-body">
                            <div class="join-search">
                                <input type="text" v-model="joinData.value"><span><i class="fa fa-search"></i></span>
                            </div>
                            <div class="join-search-body">
                                <div v-for="row in displayJoinList" @click="selectJoinGroup(row)" :class="[row.isSelected?'is-active':'']">
                                    <span>{{row.name}}</span><br>
                                    <span>{{row.user.name}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>                
            </template>
        </tip-dialog>
    </div>
</template>

<script>
    import listTable from '../table/table'
    import infoPanel from '../common/infoPanel'
    import axios from 'axios'
    import tipDialog from '../common/tipDialog'
    import _ from 'lodash'

    export default {
        created() {
            this.getList()
        },
        components: {
            listTable,
            infoPanel,
            tipDialog
        },
        methods: {
            getList() {
                this.$httpMethods.lessonGroup.queryLessonGroupsByCurrentUser().then(res => {
                    if(res && res.status === 200) {
                        this.srcData = res.data || []
                    }
                })
            },
            getInfo(row, event) {
                axios.all([this.$httpMethods.lessonGroup.getLessonGroupById(row.$$src.id),this.$httpMethods.homework.queryShowedHomeworksByGroupId(row.$$src.id)])
                .then(axios.spread((infoList,commentData) => {
                    this.isDetail = true
                    this.detailIndex = 'baseInfo'
                    this.detailData.infoList = infoList.data
                    this.commentData = commentData.data
                }))
            },
            detailEdit(row) {

            },
            remove(row) {
                
            },
            refresh() {
                this.$httpMethods.lessonGroup.queryLessonGroupsByCurrentUser().then(res => {
                    if(res && res.status === 200) {
                        this.srcData = res.data || []
                        this.$notify({
                            type: 'success',
                            title: this.$t('common.success'),
                            message: this.$t('message.refresh')
                        })
                    }
                })
            },
            canel() {
                this.isDetail = false
            },
            joinSearch(text, list) {
                return list.filter(item => {
                    return ~item.name.indexOf(text) || ~item.user.name.indexOf(text)
                })
            },
            changeIndex(index) {
                this.detailIndex = index
            },
            getCommentInfo() {

            },
            canelComment() {

            },
            selectJoinGroup(row) {
                let list = _.clone(this.joinData.list)
                this.joinData.selectId = row.id

                list.forEach(item => {
                    item.isSelected = item.id === this.joinData.selectId
                })
                this.joinData.list = list
            },
            joinLessonGroup() {
                this.$httpMethods.lessonGroup.queryLessonGroupByNotIn().then(res => {
                    if(res && res.status === 200) {
                        this.joinData.list = res.data || []
                        this.joinData.list.forEach(item => {
                            item.isSelected = false
                        })
                        this.joinData.visible = true
                    }
                })
            },
            showStar(value) {
                let items = []
                const isOdd = value % 2 === 1
                const onNum = parseInt(value / 2)
                const offNum = isOdd ? 4 - onNum : 5 - onNum
                for (let i = 0; i < onNum; i++) {
                    items.push(<i class="el-rate__icon el-icon-star-on rate-on"></i>)
                }
                if (isOdd) {
                    items.push(<i class="el-rate__icon el-icon-star-on"><i class="el-rate__decimal el-icon-star-on rate-half"></i></i>)
                }
                for (let i = 0; i < offNum; i++) {
                    items.push(<i class="el-rate__icon el-icon-star-on"></i>)
                }
                return (<span title={value}>{items}</span>)
            }
        },
        computed: {
            displayJoinList() {
                return this.joinSearch(this.joinData.value, this.joinData.list)
            }
        },
        data() {
            return {
                isDetail: false,
                detailIndex: 'baseInfo',
                selectId: 0,
                joinData: {
                    visible: false,
                    title: this.$t('join'),
                    type: 'add',
                    value: '',
                    list: [],
                    cancel: () => {
                        this.joinData.visible = false
                        this.joinData.value = ''
                        this.joinData.list = []
                    },
                    confirm: () => {
                        this.$httpMethods.lessonGroup.addMeToGroups(this.joinData.selectId).then(res => {
                            if(res && res.status === 200) {
                                this.$notify({
                                    type: 'success',
                                    title: this.$t('common.success'),
                                    message: this.$t('message.join')
                                })
                                this.getList()
                                this.joinData.visible = false
                                this.joinData.value = ''
                                this.joinData.list = []
                            }
                        })
                    }
                },
                detailData: {
                    infoList: {},
                    infoHandler: [{
                        title: this.$t('baseInfo'),
                        fields: [{
                            name: 'name',
                            showName: this.$t('lessonGroup.title')
                        }, {
                            name: 'user',
                            showName: this.$t('lessonGroup.teacherName'),
                            format: data => {
                                return data.user.name
                            }
                        }, {
                            name: 'users',
                            showName: this.$t('lessonGroup.studentNum'),
                            format: data => {
                                return data.users?data.users.length:0
                            }
                        }]
                    }],
                    studentSelected: {
                        list: [],
                        fields: [{
                            name: 'username',
                            showName: this.$t('username')
                        }, {
                            name: 'name',
                            showName: this.$t('studentName')
                        }]
                    },
                    manageFields: [{
                        name: 'homeworkName',
                        showName: this.$t('homeworkName')
                    }, {
                        name: 'submittedNum',
                        showName: this.$t('submitIfo'),
                        formatter: (row) => {
                            return row.$$src.submittedNum + '/' + row.$$src.lessonGroupDTO.memberLimit
                        }
                    }, {
                        name: 'averageScore',
                        showName: this.$t('score'),
                        formatToVnode: this.showStar
                    }, {
                        name: 'createTime',
                        showName: this.$t('createTime')
                    }],
                    manageOperators: [{
                        name: this.$t('compile'),
                        handler: this.detailEdit
                    }],
                    commentFields: [{
                        name: 'homeworkName',
                        showName: this.$t('homeworkName'),
                        showOverflowTooltip: true
                    }, {
                        name: 'student',
                        path: 'user.name',
                        showName: this.$t('student'),
                        width: 100
                    }, {
                        name: 'score',
                        showName: this.$t('score'),
                        formatToVnode: this.showStar,
                        width: 180
                    }, {
                        name: 'scoreTime',
                        showName: this.$t('scoreTime'),
                        width: 180
                    }],
                    commentOperators: [{
                        name: this.$t('info'),
                        handler: this.getCommentInfo
                    }, {
                        name: this.$t('canelComment'),
                        handler: this.canelComment
                    }],
                    studentFields: [{
                        name: 'studentName',
                        path: 'user.name',
                        showName: this.$t('studentName')
                    }, {
                        name: 'studentName',
                        path: 'user.name',
                        showName: this.$t('studentName')
                    }]
                },
                srcData: [],
                manageData: [],
                commentData: [],
                fields: [{
                    name: 'name',
                    showName: this.$t('name')
                }, {
                    name: 'memberLimit',
                    showName: this.$t('memberLimit')
                }, {
                    name: 'username',
                    path: 'user.username',
                    showOverflowTooltip: true,
                    showName: this.$t('teacherName')
                }, {
                    name: 'score',
                    showName: this.$t('score'),
                    formatToVnode: this.showStar
                }],
                operators: [{
                    name: this.$t('info'),
                    handler: this.getInfo
                }, {
                    name: this.$t('remove'),
                    handler: this.edit,
                    renderFn: (rowData) => {
                        // if (rowData.$$src.memberLimit > 30) {
                        //     return <span>xxxx</span>
                            //              return <div onClick={e => this.edit(rowData, e)}>xxxx</div>
                        // }
                        // return ''
                        return ''
                    }
                }]
            }
        }
    }
</script>

<style scoped>
    .rate-on {
        color: rgb(247, 186, 42);
    }
    .rate-half {
        color: rgb(247, 186, 42);
        width: 50%;
    }
    .btn-canel {
        width: 90px;
        height: 40px;
        background-color: rgba(12, 146, 254, 0.05);
        border-radius: 1px;
    }
    .btn-transparent {
        width: 60px;
        height: 30px;
        border-radius: 2px;
        border: solid 1px #0c92fe;
        background-color: #f5f7f8;
        padding: 0;
        font-size: 12px;
        color: #018fff;
    }
    .container-header {
        box-shadow: 1px 1px 2px 0px rgba(4, 4, 4, 0.06);
        padding: 10px 25px 5px 20px;
        margin-bottom: 2px;
    }
    .container-aside{
        background-color: #fafbfb;
        min-height: 100%;
        float: left;
        width: 220px;
    }
    .container-main {
        float: left;
        height: 100%;
        width: calc(100% - 221px);
        border-left: rgba(105, 110, 121, 0.3) 1px solid;
    }
    .container-main .table-warpper{
        width: 1220px;
        padding: 40px;
    }
    .container-main .container-main-detail .table-warpper {
        width: 96%;
        padding: 20px;
    }
    .btn-gradient.btn-sm {
        width: 136px;
        padding: 0;
        font-size: 14px;
    }
    .el-menu {
        border-right: none;
    }
    .el-menu-vertical-demo {
        width: 220px;
    }
    .el-menu-vertical-demo .el-menu-item:nth-child(2n) {
        background-color: #f5f7f8;
    }
    .el-menu-vertical-demo .el-menu-item:nth-child(2n+1) {
        background-color: #fafbfb;
    }
</style>

<i18n>
    {
        "zh": {
            "lessonGroup": {
                "name": "课程组管理",
                "title": "课程组名称:",
                "teacherName": "授课老师:",
                "studentNum": "学生数量:"
            },
            "memberLimit": "人数",
            "teacherName": "教师名",
            "name": "名称",
            "edit": "修改",
            "remove": "删除",
            "score": "评分",
            "canel": "返回",
            "operate": "操作",
            "info": "查看",
            "compile": "编辑",
            "outTime": "失效时间:",
            "scoreTime": "批改时间",
            "createTime": "发布时间",
            "createTime": "创建时间:",
            "baseInfo": "基本信息",
            "homework": "作业",
            "release": "发布作业",
            "comment": "作业展示",
            "statistics": "作业统计",
            "homeworkName": "作业名称",
            "submitIfo": "提交信息",
            "submittedNum": "提交数量:",
            "canelComment": "取消展示",
            "student": "学生",
            "username": "用户名",
            "studentSelected": "选课学生",
            "studentManage": "学生管理",
            "studentName": "学生姓名",
            "telephoneNo": "手机号码",
            "email": "电子邮箱",
            "join": "加入课程组",
            "joinTip": "申请加入课程组需要等待授课老师审核，通过后可加入到对应课程组",
            "message": {
                "refresh": "刷新成功",
                "join": "申请加入课程组成功"
            }
        }
    }
</i18n>



